

footer{
  width: 100%;
  bottom: 0;
  left: 0;
  background: #111;
}
footer .content{
  margin: auto;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
    padding-left: 40px;
}
footer .content p,a{
  color: #eee;
    font-size: 14px;
}
footer .content .box{
  width: 33%;
  transition: all 0.4s ease;
}
footer .content .topic{
  font-size: 18px;
  font-weight: 600;
  color: #FC5B62;
  margin-bottom: 16px;

}

footer .content .upper p{
 line-height: 30px;
    font-size: 14px;
}

footer .content .lower .phone{
  margin: 24px 0 5px 0;
}
footer .content .lower .email{
  margin: 24px 0 5px 0;
}
footer .content .lower i{
  padding-right: 16px;
}
footer .content .middle{
  padding-left: 80px;
}
footer .content .middle a{
  line-height: 32px;
}
footer .content .right input[type="text"]{
  height: 45px;
  width: 100%;
  outline: none;
  color: #d9d9d9;
  background: #000;
  border-radius: 5px;
  padding-left: 10px;
  font-size: 17px;
  border: 2px solid #222222;
}
footer .content .right input[type="submit"]{
  height: 42px;
  width: 100%;
  font-size: 18px;
  color: #eee;
  background: #FC5B62;
  outline: none;
  border-radius: 5px;
  letter-spacing: 1px;
  cursor: pointer;
  margin-top: 12px;
  border: 2px solid #FC5B62;
  transition: all 0.3s ease-in-out;
  font-weight: bold;
}
.content .right input[type="submit"]:hover{
  background: none;
  color: #FC5B62;
}
footer .content .media-icons a{
  font-size: 16px;
  height: 45px;
  width: 45px;
  display: inline-block;
  text-align: center;
  line-height: 43px;
  border-radius: 5px;
  border: 2px solid #222222;
  margin: 30px 5px 0 0;
  
}
.content .media-icons a:hover{
  border-color: #FC5B62;
}
footer .bottom{
  width: 100%;
  text-align: right;
  color: #d9d9d9;
  padding: 0 40px 5px 0;
    font-size: 13px;
}
footer .bottom a{
    color: #FC5B62;
    font-size: 13px;
}

footer a:hover{
  color: #FC5B62;
}
